import React, { Component } from "react";
import Counter from "./counter";
import bridge from "@vkontakte/vk-bridge";

class Counters extends Component {
  state = {
    counters: [{ id: 1, value: 0, name: "" }],
    fetchedUser: null,
    TextInput: React.createRef(),
  };
  constructor(props) {
    super(props);
    // Эта привязка обязательна для работы `this` в колбэке.
    this.saveResult = this.saveResult.bind(this);
  }

  getUserProfile() {
    bridge
      .sendPromise("VKWebAppGetUserInfo")
      .then((data) => {
        // Обработка события в случае успеха
        console.log(data.id);
      })
      .catch((error) => {
        // Обработка события в случае ошибки
      });
  }

  saveResult() {
    //bridge.send("VKWebAppStorageSet", { key: "example", value: "my_value" });
    bridge.send("VKWebAppShare", { link: "https://vk.com" });
    console.log(this.state.TextInput.current.value);
  }
  getResult() {
    // bridge.send("VKWebAppStorageGet", { keys: "example" });
    bridge.send("VKWebAppShowStoryBox", {
      background_type: "none",
      stickers: [
        {
          sticker_type: "renderable",
          sticker: {
            can_delete: 0,
            content_type: "image",
            url: "https://i.imgflip.com/2k3dv4.jpg",
          },
        },
      ],
    });
  }

  newItem = () => {
    // this.setState({ counters: this.state.counters.length + 1 });
    this.setState({
      counters: this.state.counters.concat({
        id: this.state.counters.length + 1,
        value: 0,
      }),
    });
    console.log(this.state.counters);
  };

  render() {
    return (
      <div>
        <button onClick={this.newItem} className="btn btn-primary btn-sm">
          Add
        </button>

        <button
          onClick={this.getUserProfile}
          className="btn btn-primary btn-sm"
        >
          GetProfile
        </button>

        <input type="text" placeholder="Text" ref={this.state.TextInput} />

        <button onClick={this.saveResult} className="btn btn-primary btn-sm">
          Save
        </button>
        <button onClick={this.getResult} className="btn btn-primary btn-sm">
          Load
        </button>
        {this.state.counters.map((counter) => (
          <Counter key={counter.id} value={counter.value} selected={true} />
        ))}
      </div>
    );
  }
}

export default Counters;
